.ind-project-container {
    width: 45%;
    height: auto;
    background-color: rgb(255, 255, 255);
    border-radius: 0.75rem;
    transition: all 0.25s;
    height: 3rem;
    padding: 1.5%;
    margin: 0.5%;
    z-index: 2;
    cursor: pointer;
}
.ind-project-container-mobile {
    width: 95%;
    height: auto;
    background-color: rgb(255, 255, 255);
    border-radius: 0.75rem;
    transition: all 0.25s;
    height: 3rem;
    padding: 1.5%;
    margin: 0.5%;
    z-index: 2;
    cursor: pointer;
}
/* .ind-project-container:hover {
    height: 3.25rem;
} */
.ind-project-container p {
    font-size: 1rem;
}
#grow {
    height: 10rem;
    /* max-height: auto; */
}
#grow-mobile {
    height: 15rem;
    /* max-height: auto; */
}

.always-visible-section {
    width: 100%;
    font-size: 1rem;
    display: flex;
    height: 3rem;
    align-items: center;
    /* padding: 1rem; */
}
.always-visible-section img {
    height: 100%;
    width: auto;
}
.always-visible-section h2 {
    margin: 0px;
    /* padding: 0.5rem; */
    padding-left: 0.5rem;
    font-size: 2rem;
}

.mobile-project-title {
    margin: 0px !important;
    /* padding: 0.5rem; */
    padding-left: 0.75rem !important;
    font-size: 1.5rem !important;
}
.slideshow-pill-container {
    position: absolute;
    bottom: 1rem;
    width: 4.25rem;
    height: 1.5rem;
    border-radius: 1rem;
    left: 1rem;
    background-color: rgba(255, 255, 255, 0.3);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    z-index: 5;
    backdrop-filter: blur(10px);
    box-shadow: 0 8px 30px rgb(0 0 0 / 12%);
}
.large-container {
    bottom: calc(1rem + 5%);
    right: 1rem;
    left: auto;
}
.filledCircle {
    width: 0.75rem;
    height: 0.75rem;
    border-radius: 0.5rem;
    background: white;
    z-index: 4;
    /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8); */

    top: calc(100% - 2rem);
}
.transparentCircle {
    width: 0.75rem;
    height: 0.75rem;
    border-radius: 0.5rem;
    background: rgba(255, 255, 255, 0.75);
    /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8); */
    z-index: 4;
    top: calc(100% - 2rem);
}
