@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: "Open Sans", sans-serif;
    --accent: rgb(77, 150, 199);
}

::selection {
    background: rgb(181, 218, 240);
}
html {
    scroll-behavior: smooth;
}

.flex {
    display: flex;
}
.home {
    flex-direction: row;
    height: 105vh;
    background-color: rgb(255, 255, 255);

    width: 100%; /* this one change gets rid of scrollbars on windows lol */
}
.title-container {
    /* width: 50%; */
    height: auto;
    color: black;
    position: absolute;
    left: 0%;
    padding-left: 5rem;
    top: calc(100% - 7.5rem);
    z-index: 5;
    color: white;
    text-align: left;
    display: flex;
}
.main-chevron-container {
    margin-top: 1.5rem;
    margin-left: 1rem;
    height: 4rem;
    width: 5rem;
    transition: -webkit-transform 0.75s;
    transition: transform 0.75s;
    transition: transform 0.75s, -webkit-transform 0.75s;
}
.main-chevron-container:hover {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
.main-chevron {
    /* width: 100%; */
    height: 100%;
    width: auto;
    /* filter: invert(); */
    -webkit-filter: invert() drop-shadow(0 8px 30px rgb(0 0 0 / 50%));
            filter: invert() drop-shadow(0 8px 30px rgb(0 0 0 / 50%));
}
.main-title {
    font-size: 5rem;
    margin: 0px;
    text-shadow: 0 8px 30px rgb(0 0 0 / 50%);
}
/* .image-container {
    width: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
} */
.slide-img {
    /* width: 100%; */
    width: 100%;
    height: 100%;
    object-fit: cover;
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
    position: absolute;
    background-size: cover;
    background-position: center center;

    transition: 0.125s -webkit-transform;

    transition: 0.125s transform;

    transition: 0.125s transform, 0.125s -webkit-transform;
    top: 0%;
    left: 0%;
    cursor: pointer;
}
.actual-img-container {
    height: 100%;
    width: 100%;
    position: relative;
    overflow: hidden;
}
.triangle-beneath {
    position: absolute;
    top: 100%;
    height: calc(5% + 1px);
    z-index: 5;
    fill: var(--accent);
}
.triangle-beneath2 {
    position: absolute;
    top: 105%;
    height: calc(5% + 1px);
    z-index: 5;
    fill: white;
    background-color: var(--accent);
}
.title-container-mobile {
    height: auto;
    color: black;
    position: absolute;
    left: 0%;
    padding-left: 1rem;
    top: calc(100% - 15rem);
    z-index: 5;
    color: white;
    text-align: left;
    display: flex;
}

.ind-project-container {
    width: 45%;
    height: auto;
    background-color: rgb(255, 255, 255);
    border-radius: 0.75rem;
    transition: all 0.25s;
    height: 3rem;
    padding: 1.5%;
    margin: 0.5%;
    z-index: 2;
    cursor: pointer;
}
.ind-project-container-mobile {
    width: 95%;
    height: auto;
    background-color: rgb(255, 255, 255);
    border-radius: 0.75rem;
    transition: all 0.25s;
    height: 3rem;
    padding: 1.5%;
    margin: 0.5%;
    z-index: 2;
    cursor: pointer;
}
/* .ind-project-container:hover {
    height: 3.25rem;
} */
.ind-project-container p {
    font-size: 1rem;
}
#grow {
    height: 10rem;
    /* max-height: auto; */
}
#grow-mobile {
    height: 15rem;
    /* max-height: auto; */
}

.always-visible-section {
    width: 100%;
    font-size: 1rem;
    display: flex;
    height: 3rem;
    align-items: center;
    /* padding: 1rem; */
}
.always-visible-section img {
    height: 100%;
    width: auto;
}
.always-visible-section h2 {
    margin: 0px;
    /* padding: 0.5rem; */
    padding-left: 0.5rem;
    font-size: 2rem;
}

.mobile-project-title {
    margin: 0px !important;
    /* padding: 0.5rem; */
    padding-left: 0.75rem !important;
    font-size: 1.5rem !important;
}
.slideshow-pill-container {
    position: absolute;
    bottom: 1rem;
    width: 4.25rem;
    height: 1.5rem;
    border-radius: 1rem;
    left: 1rem;
    background-color: rgba(255, 255, 255, 0.3);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    z-index: 5;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    box-shadow: 0 8px 30px rgb(0 0 0 / 12%);
}
.large-container {
    bottom: calc(1rem + 5%);
    right: 1rem;
    left: auto;
}
.filledCircle {
    width: 0.75rem;
    height: 0.75rem;
    border-radius: 0.5rem;
    background: white;
    z-index: 4;
    /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8); */

    top: calc(100% - 2rem);
}
.transparentCircle {
    width: 0.75rem;
    height: 0.75rem;
    border-radius: 0.5rem;
    background: rgba(255, 255, 255, 0.75);
    /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8); */
    z-index: 4;
    top: calc(100% - 2rem);
}

.about-section-container {
    display: flex;
    width: 100%;
    height: 75vh;
    flex-direction: row;
    justify-content: center;
    padding-top: 10%;
}
.about-slideshow-container {
    width: 30%;
    height: 100%;
    /* padding-top: 10%; */
}
.about-text-container {
    /* padding: 10%; */
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    width: 50%;
    height: 100%;
    font-size: 1.5rem;
}
.link {
    color: black;
    -webkit-text-decoration: underline solid;
            text-decoration: underline solid;
    -webkit-text-decoration-color: var(--accent);
            text-decoration-color: var(--accent);
}
.no-flex {
    display: block;
    height: auto;
}
.full-width {
    width: 85%;
    margin-left: 7.5%;
}
.full-width-slideshow {
    width: 85%;
    margin-left: 7.5%;
    height: 75vh;
}

html {
    --accent-bg-color: rgb(236, 236, 236);
}
.triangle-to-projects {
    height: 8%;
    width: 100%;
    fill: rgb(236, 236, 236);
    fill: var(--accent-bg-color);
    position: relative;
    top: calc(-8%);
    z-index: 0;
    /* background-color: white; */
}
.complete-project-container {
    height: 120vh;
    width: 100%;
    margin-top: 10vh;
    background-color: rgb(236, 236, 236);
    background-color: var(--accent-bg-color);
}
.project-containers {
    display: flex;
    width: 80%;
    margin-left: 10%;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}
.project-title-container {
    width: 80%;
    margin-left: 10%;
    font-size: 2rem;
}

.triangle-to-contact {
    height: 8vh;
    width: 100%;
    fill: var(--accent);
    z-index: 0;

    position: relative;
    top: -8vh;
    /* background-color: white; */
}
.total-contact-div {
    height: auto;
    background-color: var(--accent);
}
.icon-container {
    width: 80%;
    margin-left: 10%;
    height: 7rem;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.icon-container svg {
    width: 5rem;
    height: 5rem;
    fill: black;
    transition: all 0.25s;
}

.icon-container svg:hover {
    width: 6rem;
    height: 6rem;
}
.final-title {
    margin-top: 5rem;
    text-align: center;
    padding-bottom: 3rem;
    margin-bottom: 0px;
}

.navbar-total {
    width: 100%;
    background-color: white;
    position: fixed;
    top: 0%;
    left: 0%;
    height: 5rem;
    z-index: 6;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-shadow: 0px 0px 5px rgb(0, 0, 0, 0.8);
}
.navbar-total a {
    font-size: 1.5rem;
    margin: 0px;
    padding: 1rem;
    float: left;
    font-weight: normal;
    transition: all 0.25s;
    cursor: pointer;
    color: black;
    text-decoration: none;
}
.navbar-total a:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 1rem;
}
.navbar-total img {
    height: 3rem;
    width: 3rem;
    padding-left: 2rem;
    float: left;
}
.additional-things {
    position: fixed;
    top: 5rem;
    background-color: white;
    z-index: 6;
    display: flex;
    flex-direction: column;
    width: 100%;
}
.additional-things a {
    font-size: 1.5rem;
    margin: 0px;
    padding: 1rem;
    font-weight: normal;
    transition: all 0.25s;
    cursor: pointer;
    color: black;
    width: calc(100% - 2rem);
    text-decoration: none;
    z-index: 6;
}

